<template>
  <button
    @click="sharePet"
    class="share-button">
    分享我的宠物
  </button>
</template>

<script>
export default {
  methods: {
    sharePet() {
      // 这里可以调用微信分享的API
      if (typeof wx !== 'undefined') {
        wx.shareAppMessage({
          title: '我分享了我的宠物！',
          path: '/pages/share/index',
          success: () => {
            console.log('分享成功');
          },
          fail: error => {
            console.error('分享失败', error);
          },
        });
      } else {
        console.warn('微信API未加载');
      }
    },
  },
};
</script>

<style scoped>
.share-button {
  background-color: #1aad19;
  color: white;
  border: none;
  padding: 10px 20px;
  border-radius: 5px;
  cursor: pointer;
  font-size: 16px;
}
.share-button:hover {
  background-color: #0a9d14;
}
</style>
